@import 'utils';

.movies {
  margin-top: 2.4rem;

  &__title {
    font-size: 2rem;
    font-weight: 500;
  }

  &-filter {
    margin-top: 1.2rem;

    &__title {}

    &__line {}

    &__search {
      margin-top: 0.8rem;
      display: flex;
      align-items: center;
      height: 2rem;
  
      & svg {
        height: 1.6rem;
        width: 1.6rem;
      }
  
      & input {
        margin-left: 1rem;
        height: 100%;
        color: #FFF;
        font-size: 1.6rem;
  
        &::placeholder {
          color: #fff;
          opacity: 0.7;
        }
      }
    }
  }
  
}

@include minw($sm-pc) {
  .movies {
    margin-top: 4.8rem;

    &__title {
      font-size: 2.4rem;
    }

    &-filter {
      margin-top: 1.6rem;
      width: 100%;
      display: flex;
      align-items: center;
      

      &__title {}

      &__line {
        margin: 0 2rem;
        height: 1px;
        flex-shrink: 0;
        flex-grow: 1;
        background: #FFF;
        opacity: 0.6;

      }

      &__filterby {
        width: 24rem;
        color: #000;
        cursor: pointer;
      }

      &__search {
        margin-top: 0;
      }
    }
  }
}